"use client";

import React, { createContext, useContext, useEffect, useState } from "react";
import type { Lang } from "@/lib/langCookie";
import { getLangCookie, setLangCookie } from "@/lib/langCookie";

type LanguageCtx = {
  lang: Lang;
  setLang: (l: Lang) => void;
  ready: boolean; // cookie loaded
};

const Ctx = createContext<LanguageCtx | null>(null);

export function LanguageProvider({ children }: { children: React.ReactNode }) {
  const [lang, setLangState] = useState<Lang>("so"); // default Somali
  const [ready, setReady] = useState(false);

  useEffect(() => {
    const saved = getLangCookie();
    if (saved) setLangState(saved);
    setReady(true);
  }, []);

  const setLang = (l: Lang) => {
    setLangState(l);
    setLangCookie(l);
  };

  return <Ctx.Provider value={{ lang, setLang, ready }}>{children}</Ctx.Provider>;
}

export function useLanguage() {
  const v = useContext(Ctx);
  if (!v) throw new Error("useLanguage must be used inside LanguageProvider");
  return v;
}
